.App {
  text-align: center;
  font-family: 'Nanum Gothic'
}
.modal {
  background: linear-gradient(to right, #ff7e5f, #feb47b);
  border-radius: 15px;
  padding: 20px;
  max-width: 600px;
  /* 최대너비설정*/
  margin: 20px auto;

}
.modal h1 {
  color: #fff;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
  font-size: 28px;
}

.modal .content {
  font-size: 18px;
}
.modal .button {
  color: #fff;
  border:none;
  /* padding: 15px 32px; */
  width: 200px;
  height: 45px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 20px 0;
  cursor: pointer;
  background-color: #000;
  border-radius: 50px;
}
.modal .button:hover {
  background-color: #2b2a2a;
}
.modal-backdrop {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
}
.modal-backdrop p+p {
  color: red;
  font-weight: bold;
  font-size: 20px;
}


@media (max-width:760px){
  .modal {
    max-width:80%;
  }
}